import { Component, OnInit, Input, Output } from '@angular/core';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.scss']
})
export class ChartsComponent implements OnInit {
  public dataSource: any;
  public chart: any;

  // @Input('gaugeChart2') dataSource: any;
  constructor() {
    this.dataSource = {
      chart: {
        caption: '',
        lowerLimit: '0',
        upperLimit: '100',
        showValue: '1',
        numberSuffix: '%',
        theme: 'fusion',
        showToolTip: '0'
      },
      // Gauge Data
      colorRange: {
        color: [
          {
            minValue: '0',
            maxValue: '100',
            code: '#F2726F'
          },
        ]
      },
      dials: {
        dial: [
          {
            value: 68
          }
        ]
      }
    };
  }

  ngOnInit() {
  }

  initialized($event) {
    console.log('Initialized');
    this.chart = $event.chart; // Storing the chart instance
  }

  updateGaugeValue(data) {
    console.log('Update', data);
    setTimeout(() => {
      console.log(this.chart);
      this.chart.setChartAttribute({caption: data.caption});
      this.chart.feedData('&value=' + data.value);
    }, 200);
  }
}
