import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SharedService {

  constructor() { }

  getLabel(keyword) {
    if (keyword) {
      keyword = keyword.split('_').join(' ');
      return this.capitalizeFirstLetter(keyword);
    } else {
      return keyword;
    }
  }

  capitalizeFirstLetter(string) {
    if (string !== '') {
      return string.charAt(0).toUpperCase() + string.slice(1);
    } else {
      return string;
    }
  }

  removeFirstWord(field, val) {
    return field.replace(val, '');
  }

  getFixed(num) {
    // let num1 = parseFloat(num);
    let n = (parseFloat(num) < 0) ? Math.floor(Math.abs(parseFloat(num)) * 100000) * -1 / 100000 : parseFloat(num).toFixed(4);
    n = (n !== 'NaN') ? n : '-';
    // console.log(num1, n);
    return (typeof num === 'string') ? num : n;
  }
}
