import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';

import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private headers: any;
  constructor(private _http: HttpClient) {
    this.headers = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      })
    };
  }

  GetRequest(Path): Observable<any> {
    return this._http.get(`${environment.baseUrl}/${Path}`, this.headers);
  }

  PostRequest(Path, data): Observable<any> {
    return this._http.post(`${environment.baseUrl}/${Path}`, JSON.stringify(data), this.headers);
  }

  getCompanyPortfolio(data): Observable<any> {
    return this.PostRequest('getCompanyPortFolio', data);
  }
}
