import { Component, OnInit, ViewChild} from '@angular/core';
import { ApiService } from '../services/api.service';
import { SharedService } from '../services/shared.service';

import { environment } from '../../environments/environment';

import {ChartsComponent} from '../components/charts/charts.component';
import * as FusionCharts from 'fusioncharts';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit {
  // @ViewChild(ChartsComponent) private _guageChart;
  @ViewChild('p11') private _guageChart1;
  @ViewChild('q1b') private _guageChart2;
  public title: string;
  public gaugeChart1 = {caption: 'P11', color: '#F2726F', value: 0};
  public gaugeChart2 = {caption: 'Q1 B', color: '#F2726F', value: 0};
  objectKeys = Object.keys;

  constructor(private _api: ApiService, public _shared: SharedService) {}

  public portfolioData: any;
  public basicData: object;
  public otherData: object;
  public otherDataField = [];
  public otherDataCols = [];
  public validationMatrices = [];
  public validationMCols = [{title: 'latest_', name: 'Latest'}, {title: 'avg_', name: 'Average'}];
  public validationMFields = [];

  ngOnInit() {
    this.getPortfolio();
  }

  getPortfolio() {
    this._api.getCompanyPortfolio({ co_code: '34' }).subscribe((response) => {
      this.portfolioData = response;
      this.basicData = this.portfolioData.basic_data;
      this.otherData = this.portfolioData.other_data;
      this.validationMatrices = this.portfolioData.validation_matrices;
      this.gaugeChart1.value = parseInt((this.portfolioData.part_of_indices.p11 * 100).toFixed(2), 10);
      this.gaugeChart2.value = parseInt((this.portfolioData.part_of_indices.q1b * 100).toFixed(2), 10);
      setTimeout(() => {
        this._guageChart1.updateGaugeValue(this.gaugeChart1);
        this._guageChart2.updateGaugeValue(this.gaugeChart2);
      }, 100);
      this.objectKeys(this.otherData).forEach((val, key) => {
        console.log(key);
          this.otherDataField.push({
            title: val, name: this._shared.getLabel(this._shared.removeFirstWord(val, 'latest_'))
          });
      });
      this.objectKeys(this.otherData[this.otherDataField[0].title]).reverse().forEach((val, key) => {
        console.log(key);
          this.otherDataCols.push({
            title: val, name: this._shared.getLabel(val)
          });
      });
      this.validationMFields.push(
        {title: 'ev_per_ebitda', name: this._shared.getLabel('ev_per_ebitda')},
        {title: 'p_per_s', name: this._shared.getLabel('p_per_s')},
        {title: 'pe_ratio', name: this._shared.getLabel('pe_ratio')},
        {title: 'pb_ratio', name: this._shared.getLabel('pb_ratio')});
      console.log(this.otherDataField, this.otherDataCols);
    });
  }

  getDate(dt) {
    const d = new Date(dt);
    return (environment.months[d.getMonth()] + ' ' + d.getDate() + ', ' + d.getFullYear());
    // else return dt;
  }
}
